header{
    width: 100%;
    display: flex;
    justify-content: space-between;
    align-items: center;
    /* padding-inline: 15rem; */
    /* padding-block: 7rem; */
    padding: 2rem 13rem;
}

.log{
    width: 120px;
    height: 70px;
    /* background-color: red; */
    background: url("https://www.urbansciencemep.com/_next/static/images/blackLogo-10fc2f582f155efcc2c792a97edc1f33.png");
    background-size: 100% 100%;
    background-repeat: no-repeat;
}

ul{
    display: flex;
    gap: 50px;
}

button{
    padding: 13px 30px;
    background-color: #596AFF;
    border-radius: 45px;
    color: white;
    border: none;
    outline: none;
    font-weight: 600;
}

li,a{
    list-style: none;
    text-decoration: none;
}